/* exo-2-100 - cyrillic */
@font-face {
  font-family: "Exo 2";
  font-style: normal;
  font-weight: 100;
  src: local(""), url("../fonts/exo-2-v9-cyrillic-100.woff2") format("woff2"), url("../fonts/exo-2-v9-cyrillic-100.woff") format("woff"); }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.common {
  color: #ffffff;
  background-color: #000000;
  background-image: url("../img/background.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

.products {
  margin: auto;
  padding: 0 10px;
  width: 100%;
  max-width: 1120px; }

.products__title {
  margin-top: 0;
  margin-bottom: 26px;
  font-size: 36px;
  font-weight: 100;
  font-family: "Exo 2", "Open Sans", sans-serif;
  text-align: center; }

.products__list {
  padding: 0;
  display: flex; }

@media (max-width: 1023px) {
  .products__list {
    flex-wrap: wrap;
    justify-content: center; } }

@media (max-width: 767px) {
  .products {
    min-width: 320px; }
  .products__list {
    padding: 0;
    align-items: center;
    flex-direction: column; } }

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .products {
    margin: 0; } }

.card {
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  list-style: none; }
  .card:last-child {
    margin-right: 0; }

.card__inner {
  position: relative;
  padding: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
  height: 100%;
  color: #666666;
  border-radius: 12px;
  background-image: url("../img/bg-card.svg");
  clip-path: polygon(13.3% 0, 100% 0, 100% 107%, 0 107%, 0 8.9%);
  cursor: pointer; }

.card--selected .card__inner {
  background-image: url("../img/bg-card-selected.svg"); }

.card--disabled .card__inner {
  background-image: url("../img/bg-card-disabled.svg");
  cursor: default; }

.card__top {
  padding-top: 2px;
  padding-right: 44px;
  padding-left: 47px;
  padding-bottom: 18px; }

.card__description {
  margin-bottom: 17px; }
  .card__description--hover {
    display: none; }

.card--disabled .card__description {
  color: #b3b3b3;
  opacity: 0.5; }

.card__description-hover {
  display: none; }

.card--selected:hover .card__description-default--deactivate {
  display: none; }

.card--selected:hover .card__description-hover--active {
  display: block;
  color: #d91667; }

.card__title {
  margin: 0;
  margin-bottom: 12px;
  color: #000000;
  font-size: 48px;
  font-weight: 700;
  line-height: 33px; }

.card__taste {
  font-size: 24px; }

.card--disabled .card__title {
  color: #b3b3b3;
  opacity: 0.5; }

.card__options {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  list-style: none; }

.card__options-quantity {
  font-weight: 700; }

.card--disabled .card__options {
  color: #b3b3b3;
  opacity: 0.5; }

.card__img {
  display: block;
  width: 310px;
  border-radius: 8px; }

.card--disabled .card__img {
  opacity: 0.5; }

.card__weight {
  position: absolute;
  right: 16px;
  bottom: -27px;
  padding-top: 23px;
  padding-bottom: 5px;
  width: 80px;
  text-align: center;
  color: #ffffff;
  font-size: 42px;
  line-height: 22px;
  background-color: #1698d9;
  border-radius: 50%; }

.card--selected .card__weight {
  background-color: #d91667; }

.card--disabled .card__weight {
  background-color: #b3b3b3; }

.card__metric {
  font-size: 21px; }

.card__call {
  font-size: 13px;
  color: #ffffff;
  text-align: center; }
  .card__call--selected {
    display: none; }
  .card__call--disabled {
    display: none; }

.card__appeal {
  position: relative;
  font-weight: 700;
  color: #1698d9;
  text-decoration: none; }
  .card__appeal::after {
    position: absolute;
    left: 1px;
    bottom: 0;
    display: block;
    width: 80%;
    height: 1px;
    border-bottom: 1px dashed;
    content: ""; }
  .card__appeal:hover::after {
    border-bottom: 1px solid; }

.card--selected .card__call {
  display: none; }

.card--selected .card__call--selected {
  display: block; }

.card--disabled .card__call {
  display: none; }

.card--disabled .card__call--disabled {
  display: block;
  color: #ffff66; }

@media (max-width: 1023px) {
  .card {
    margin-right: 80px; }
    .card:first-child {
      margin-right: 0;
      margin-bottom: 67px;
      width: 100%; } }

@media (max-width: 767px) {
  .card {
    margin-right: 0;
    margin-bottom: 10px;
    width: 300px; }
    .card:first-child {
      margin-bottom: 10px;
      width: 300px; }
    .card:last-child {
      margin-bottom: 0; }
  .card__img {
    width: 290px; } }

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .card__top {
    margin-bottom: auto; } }

.main-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; }
